import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../../services/api.service';

@Component({
  selector: 'app-student-register',
  templateUrl: './student-register.component.html',
  styleUrls: ['./student-register.component.scss']
})
export class StudentRegisterComponent implements OnInit {
  student = {
    profilePic: '',
    name: '',
    email: '',
    password: '',
    phone: null,
  
  };
  file: any
  public imagePath;
  imgURL: any;
  ischeck = false;
  userdata = [];
  constructor(private router: Router, private service: ApiService) { }

  ngOnInit() {
  }

  onChange(event) {
    var files = event.srcElement.files;
    this.file = files[0];
    this.loadImage(this.file.type, files);
  }

  loadImage(mimeType, files) {
    if (mimeType.match(/image\/*/) == null) {
      return;
    }
    var reader = new FileReader();
    this.imagePath = files;
    reader.readAsDataURL(files[0]); 
    reader.onload = (_event) => { 
      this.imgURL = reader.result; 
    }
  }

  onBoard()
  {

if(this.student.name && this.student.email && this.student.password && this.student.phone )
{
  console.log(this.userdata.indexOf)
  this.ischeck = JSON.parse(localStorage.getItem("ischeck"));
  var temp = [];
   if(localStorage.getItem("item"))
   {
    temp=JSON.parse(localStorage.getItem("item"));
   }
  
  let data = 
  {
  "name":this.student.name,
  "email":this.student.email,
  "password":this.student.password,
  "phone":this.student.phone
  }
   
  temp.push(data)
  localStorage.setItem("ischeck","true");
  localStorage.setItem("item", JSON.stringify(temp));
console.log('helo',temp)
// localStorage.setItem("item", JSON.stringify(this.userdata));
// console.log(localStorage.getItem('item'))
//abi do values are h

  
  
//
  
  this.service.showToasterSuccess("Register Sucessfully!")
  this.router.navigate(['/studentLoginPage']);
}
else
{
  this.service.showToasterError("please enter correct field !");
}

     
  }

}
